.app{
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    
}

.bkg{
    width: 100vw;
    height: 100vh;
    backdrop-filter: blur(16px);
    background-color: #d1707070;
}

.container {
    width: 70vw;
    height: 70vh;
    margin: 15vh 15vh;
    top: 0;
    overflow: hidden; /* lo que desvorda lo elimina */
    margin-left: auto ;
    margin-right:auto ;
    
}

.app, .container{
    background-image: url('./assets/galaxya.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    position: absolute; 
    border-radius: 1.5rem;
}